import Head from 'next/head';
import { CircleLoader } from 'react-spinners';
import { useState, useEffect } from 'react';
import ItemPageStyles from '../components/styles/ItemPageStyles';
import Layout from '../components/Layout';
import data from '../static/data';
import ProjectCard from '../components/ProjectCard';
import { products } from '../static/data';

const Item = ({ query }) => {
  const [loading, setLoading] = useState(true);
  const [item, setItem] = useState({});

  useEffect(() => {
    setLoading(true);
    const pageItem = products.filter(product => {
      console.log(product.id);
      console.log(query.id);
      return product.id === query.id;
    });
    setItem({ ...pageItem[0] });
    console.log(pageItem[0]);
    setTimeout(() => {
      setLoading(false);
    }, 1000);
  }, []);

  if (loading) {
    return (
      <Layout>
        <ItemPageStyles>
          <div className="loading-page">
            <h1>
              Loading <span>MULO</span> Product
            </h1>
            <CircleLoader sizeUnit="px" size={150} color="#17D3D3" />
          </div>
        </ItemPageStyles>
      </Layout>
    );
  }
  return (
    <Layout>
      <Head>
        <title>Theran Brigowatz | Portfolio</title>
      </Head>
      <ItemPageStyles>
        {item.name === null && <h1>Oops...Item Not Found</h1>}
        <h1>{item.name}</h1>
      </ItemPageStyles>
    </Layout>
  );
};

export default Item;
